.intro .container {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: repeat(6, auto);
  grid-column-gap: 0;
  justify-items: center;
  padding-bottom: 15px;

  //@include vp-1199 {
  //  grid-template-columns: 48.6% 47.4%;
  //  grid-column-gap: 3%;
  //  justify-content: space-between;
  //}
  //
  //@include vp-767 {
  //  grid-template-columns: 1fr;
  //  grid-column-gap: 0;
  //}
}

.intro__title {
  grid-column: 1/-1;
  grid-row: 1/2;
  font-family: $ff-colus;
  font-weight: 400;
  font-size: 82px;
  line-height: 71px;
  text-align: center;
  text-transform: uppercase;
  background: linear-gradient(265.12deg, rgba(255, 255, 255, 0) 24.67%, rgba(255, 255, 255, 0) 27.37%, rgba(255, 255, 255, 0.39) 46.56%, rgba(255, 255, 255, 0) 67.13%), $color-oyster-pink;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  width: 93%;
  margin: 0 auto 0 auto;
  padding-top: 16px;

  @include vp-1199 {
    font-size: 70px;
    line-height: 59px;
  }

  @include vp-1023 {
    font-size: 50px;
    line-height: 39px;
  }

  @include vp-767 {
    font-size: 49px;
    line-height: 50px;
  }

  span {
    //display: block;
    font-size: 60px;
    line-height: 75px;
    //background: linear-gradient(265.12deg, rgba(255, 255, 255, 0) 26.65%, rgba(255, 255, 255, 0.39) 46.88%, rgba(255, 255, 255, 0) 68.55%), #D6AFAF;
    //-webkit-background-clip: text;
    //-webkit-text-fill-color: transparent;
    //background-clip: text;
    //text-fill-color: transparent;

    @include vp-1199 {
      font-size: 45px;
      line-height: 30px;
    }

    @include vp-1023 {
      font-size: 30px;
      line-height: 22px;
    }

    @include vp-767 {
      font-size: 18px;
      //line-height: 22px;
    }
  }
}

.intro__image-wrapper {
  //grid-column: 3/4;
  grid-column: 1/-1;
  grid-row: 2/-2;
  z-index: 1;
  width: 703px;
  height: 854px;
  margin-top: -194px;
  padding-left: 55px;
  display: flex;
  //justify-self: end;

  @include vp-1199 {
    //height: 390px;
  }

  @include vp-767 {
    //display: none;
  }
}

.intro img {
  max-width: 100%;
  object-fit: cover;
  object-position: top;
  display: block;
}

.intro__image-wrapper img {
  max-width: 100%;
}

.intro__description-wrapper {
  padding: 0;
  margin: 0;
  list-style: none;
  grid-column: 1/-1;
  grid-row: 2/3;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 58px;
  width: 84%;
}

.intro__description-wrapper li {
  font-family: $ff-montserrat;
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  min-height: 215px;

  &::before {
    content: "";
    position: absolute;
    width: 102px;
    height: 94px;
    z-index: 5;
    bottom: 41px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
}

.intro__description-wrapper li:nth-child(1n) {
  width: 28%;
  text-align: right;

  &::before {
    right: -55px;
    background-image: url("/img/svg/arrow-big-left.svg");


  }
}

.intro__description-wrapper li:nth-child(2n) {
  width: 28.2%;
  text-align: left;

  &::before {
    left: -55px;
    background-image: url("/img/svg/arrow-big-right.svg");
    //transform: rotate(-133.33deg);
  }
}

.intro__reviews {
  grid-column: 1/-1;
  grid-row: 4/5;
  z-index: 3;
  width: 57%;

  display: grid;
  grid-template-columns: 81% 2.5% 17% 26%;
  grid-template-rows: auto;
  align-items: start;
  margin-bottom: 7px;
}

.intro__text-review-wrapper {
  grid-column: 1/3;
  grid-row: 1/-1;

  z-index: 4;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;

  background: $color-white2;
  box-shadow: 0 13px 50px rgba(67, 67, 67, 0.05);
  border-radius: 10px;
  padding: 16px 18px 3px 25px;
  color: $color-dark-gray-transparent78;
  font-size: 15px;
  line-height: 20px;
  max-width: 569px;
  margin: -10px auto 15px 0;
  position: relative;
}

.intro__text-review-wrapper a {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  pointer-events: auto;
  content: "";
  background-color: rgba(0,0,0,0);
}

.intro__user {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin-right: 14px;
}

.intro__user div {
  width: 71px;
  height: 71px;
  background-color: $color-dark-gray;
  border-radius: 50%;
}

.intro__user img {
  max-width: 100%;
  border-radius: 50%;
  display: block;
  object-fit: cover;
}

.intro__user-name {
  font-size: 14px;
  line-height: 19px;
}

.intro__user-text {
  margin: 0;
}

.intro__link-image {
  grid-column: 2/4;
  grid-row: 1/-1;
  //grid-row: 4/5;
  z-index: 5;
  //position: relative;

  width: 131px;
  height: 127px;
  border-radius: 50%;
  background: linear-gradient(95.94deg, rgba(255, 255, 255, 0) 5.03%, rgba(255, 255, 255, 0.47) 53.24%, rgba(255, 255, 255, 0) 102.54%), linear-gradient(94.61deg, #FFEFBC -9.8%, #FFE1A5 101.57%), linear-gradient(140.94deg, #F2DEDC 22.38%, #F4DCD5 80.4%), linear-gradient(133.61deg, #B6F741 0%, #205531 115.27%);
  box-shadow: 1px 4px 0 #EBC77E;

  display: flex;
  justify-content: center;
  align-items: center;
  //margin-left: 20px;
}

.intro__link-image img {
  width: 24px;
  height: 28px;
  //и: #EBC77E;
  //box-shadow: inset 0 2px 6px rgba(174, 139, 139, 0.57);
  //border-radius: 2px;
  //transform: matrix(0, -1, -1, 0, 0, 0);
}

