.intro__link-text {
  grid-column: 4/5;
  grid-row: 1/-1;
  align-self: center;
  //grid-row: 4/5;
  z-index: 6;
  max-width: 165px;
  padding-left: 25px;
  padding-top: 7px;

  font-size: 18px;
  line-height: 24px;
  position: relative;
  color: $color-grey-transparent78;
  //display: flex;
  //align-items: center;
  //justify-content: center;

  &::after {
    content: "";
    position: absolute;
    width: 68px;
    height: 50px;
    z-index: 5;
    bottom: -19px;
    left: -30px;
    background-image: url("/img/svg/arrow-small.svg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
}

.intro__buttons {
  padding: 0;
  list-style: none;
  grid-column: 1/-1;
  grid-row: 5/6;
  z-index: 6;
  display: grid;
  column-gap: 4.5%;
  grid-template-columns: 49.4% 46%;
  max-width: 774px;
  width: 66.8%;
  margin: 0 auto 7px auto;
}

.intro__buttons li {
  font-family: $ff-montserrat;
  font-weight: 700;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.08em;
  //padding-bottom: 6px;
  //padding-right: 3px;
  //padding-left: 3px;
}

.intro__buttons-item a {
  border-radius: 10px / 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 37px 43px 37px 43px;
  text-transform: uppercase;
  width: 100%;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    width: calc(100% - 6px);
    height: 100%;
    border-radius: 10px / 20%;
    z-index: -1;
    bottom: -6px;
    left: 50%;
    transform: translateX(-50%);
    //background-repeat: no-repeat;
    //background-size: cover;
  }
}

